/**
 * WordPress dependencies
 */
import { useEffect, useRef } from '@safe-wordpress/element';
import { _x } from '@safe-wordpress/i18n';

/**
 * External dependencies
 */
import clsx from 'clsx';

/**
 * Internal dependencies
 */
import './style.scss';
import { useEditorState } from '../../../hooks';

export type JavaScriptPreviewProps = {
	readonly className?: string;
	readonly iframeId: string;
	readonly isSaving: boolean;
	readonly previewUrl?: string;
	readonly value: string;
};

const EXPECTED_ARGS = [ 'nab-javascript-previewer' ];

export const JavaScriptPreview = ( {
	className,
	iframeId,
	isSaving,
	previewUrl,
}: JavaScriptPreviewProps ): JSX.Element => {
	const ref = useRef< HTMLIFrameElement >( null );
	const [ state ] = useEditorState();

	useEffect( () => {
		const iframe = ref.current;
		if ( ! iframe ) {
			return;
		}

		const onLoad = () => {
			try {
				if ( ! previewUrl ) {
					return;
				}
				if ( ! iframe.contentWindow ) {
					return;
				}

				const originalUrl = new URL( previewUrl );
				const url = new URL( iframe.contentWindow.location.href );

				const updated = EXPECTED_ARGS.reduce( ( result, arg ) => {
					if ( url.searchParams.get( arg ) ) {
						return result;
					}
					if ( ! originalUrl.searchParams.has( arg ) ) {
						return result;
					}
					url.searchParams.set(
						arg,
						originalUrl.searchParams.get( arg ) ?? ''
					);
					return true;
				}, false );

				if ( updated && iframe ) {
					iframe.contentWindow.location.href = url.toString();
				}
			} catch {}
		};

		iframe.addEventListener( 'load', onLoad );
		return () => iframe.removeEventListener( 'load', onLoad );
	} );

	return (
		<div className={ clsx( [ className, 'nab-javascript-preview' ] ) }>
			<iframe
				id={ iframeId }
				ref={ ref }
				className={ clsx( {
					'nab-javascript-preview__iframe': true,
					[ `nab-javascript-preview__iframe--${ state.activeResolution }` ]:
						true,
					'nab-javascript-preview__iframe--is-saving': isSaving,
				} ) }
				title={ _x( 'JavaScript Preview', 'text', 'nelio-ab-testing' ) }
				src={ previewUrl }
			></iframe>
		</div>
	);
};
